<!-- 课程预约添加&编辑 -->
<template>
	<div class="container">
		<div class="course-title">
			<div>课程预约</div>
			<div>&nbsp;> 创建同步课堂</div>
		</div>
		<div class="box">
			<div class="form-wrap">
				<el-form ref="form" :model="form" label-width="90px" :rules="rules">
					<el-form-item label="课程名称:" prop="name">
						<el-input v-model="form.name"></el-input>
					</el-form-item>
					<el-form-item label="课程类型:" prop="type">
						<el-select v-model="form.type" placeholder="请选择类型">
							<el-option label="学科" value="8010"></el-option>
							<el-option label="活动" value="8011"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="教材:" prop="options">
						<el-input @click.stop.native="showSelect=true" suffix-icon="el-icon-arrow-down" v-model="form.catrgory.label"
						 placeholder="请选择相关课程">
						</el-input>
						<div v-if="showSelect" v-clickOutSide="handleOutFocusInput" class="select-wrap">
							<div class="select-items">
								<el-select v-model="stageValue" placeholder="全部学段">
									<el-option value="-1" label="全部学段"></el-option>
									<el-option v-for="item in allStageList" :key="item.code" :value="item.code" :label="item.text"></el-option>
								</el-select>
								<el-select v-model="subjectValue" placeholder="全部科目">
									<el-option value="-1" label="全部科目"></el-option>
									<el-option v-for="item in allSubject" :key="item.id" :label="item.name" :value="item.id">
									</el-option>
								</el-select>
								<el-select v-model="gradeValue" placeholder="全部年级">
									<el-option label="全部年级" value="-1"> </el-option>
									<el-option v-for="item in allGradeList" :key="item.id" :value="item.code" :label="item.text"></el-option>
								</el-select>
								<el-select v-model="versionValue" placeholder="全部版本">
									<el-option value="-1" label="全部版本"></el-option>
									<el-option v-for="item in allVersionList" :key="item.id" :value="item.id" :label="item.name"></el-option>
								</el-select>
							</div>
							<div class="select-lists">
								<el-scrollbar style="height:100%">
									<el-tree empty-text="请选择正确的学段/科目/年级/版本" :data="treeList" :props="defaultProps" @node-click="handleNodeClickCategory"></el-tree>
								</el-scrollbar>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="时间:" required>
						<el-col :span="16">
							<el-form-item prop="date">
								<el-date-picker v-model="form.date" type="datetimerange" range-separator="至" start-placeholder="开始日期"
								 end-placeholder="结束日期">
								</el-date-picker>
							</el-form-item>
						</el-col>
						<el-col :span="6" :offset="2">
							<el-form-item prop="num">
								<el-select v-model="form.num" placeholder="请选择节数">
									<el-option :label="item" :value="item" v-for="item in 10"></el-option>
								</el-select>
							</el-form-item>
						</el-col>
					</el-form-item>
					<el-form-item label="授课教室:" prop="option1">
						<div v-clickOutSide="treeShow">
							<el-input v-model="form.option1.name" readonly @focus="areaSelect" placeholder="请选择教室"></el-input>
							<div class="tree-wrap" v-show="show">
								<el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="主讲老师:" prop="teacher">
						<el-select v-model="form.teacher" placeholder="请选择老师">
							<el-option v-for="item in teacherList" :label="item.userName" :value="item.id" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="简介:">
						<el-input type="textarea" v-model="form.intro"></el-input>
					</el-form-item>
					<el-form-item label="直播:" v-if="courseOrder == 8001">
						<el-switch v-model="form.value"></el-switch>
					</el-form-item>
					<el-form-item label="课件:">
						<el-upload class="upload-demo" ref="upload" action="http://192.168.38.221:8081/uploadFile/upload.do" :on-preview="handlePreview"
						 :on-success="uploadSuccess" :on-remove="handleRemove" :before-remove="beforeRemove" multiple :limit="3"
						 :on-exceed="handleExceed" :file-list="fileList" :headers="tokenData">
							<el-button size="small" type="primary">点击上传</el-button>
							<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
						</el-upload>
					</el-form-item>
					<el-form-item label="开课方式:" v-if="courseOrder == 8002">
						<el-radio v-model="form.radio" label="8012">互动方式</el-radio>
						<el-radio v-model="form.radio" label="8013">直播方式</el-radio>
					</el-form-item>
					<el-form-item label="直播:" v-if="courseOrder == 8002&&form.radio == 8012">
						<el-switch v-model="form.value"></el-switch>
					</el-form-item>
					<el-form-item label="录制方式:" v-if="courseOrder == 8002&&form.radio == 8012">
						<el-radio v-model="form.radio1" label="8020">本地录制</el-radio>
						<el-radio v-model="form.radio1" label="8021">云录制</el-radio>
					</el-form-item>
					<el-form-item label="邀请教室:" v-if="courseOrder == 8002&&form.radio == 8012">
						<div>
							<i class="iconfont icon-add" @click="addClassroom"></i>
						</div>
						<div v-for="(item,index) in invited" style="position: relative;" v-clickOutSide="treeShow">
							<el-input v-model="item.name" readonly @focus="areaSelect1(index)" placeholder="请选择教室"></el-input>
							<div>
								<i class="iconfont icon-close" @click="classroomDel(index)"></i>
							</div>
							<div class="tree-wrap" v-show="item.show">
								<el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick1"></el-tree>
							</div>
						</div>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="onSubmit">提交</el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
export default {
    data() {
        return {
            tokenData: {
                'Authorization': localStorage.getItem('Authorization')
            },
            courseOrder:8002,
            fileList: [],
            teacherList: [],
            data: [],
            defaultProps: {
                children: 'children',
                label: 'name'
            },
            show: false,
            form: {
                catrgory: {
                    label: '',
                    value: ''
                },
                name: '',
                type: '',
                option: '',
                date: [],
                num: '',
                options: '',
                option1: {
                    name: '',
                    id: 0,
                    pId: 0
                },
                options2: [],
                teacher: '',
                intro: '',
                radio: '8012',
                value: false,
                radio1: '8020'
            },
            invited: [],
            rules: {
                name: [{
                    required: true,
                    message: '请输入课程名称',
                    trigger: 'blur'
                }],
                type: [{
                    required: true,
                    message: '请输入课程名称',
                    trigger: 'blur'
                }],
                date: [{
                    type: 'date',
                    required: true,
                    message: '请选择日期',
                    trigger: 'change'
                }],
                num: [{
                    required: true,
                    message: '请选择节数',
                    trigger: 'change'
                }],
                teacher: [{
                    required: true,
                    message: '请选择老师',
                    trigger: 'change'
                }]
            },
            showSelect: false,
            //学段list
            allStageList: [],
            stageValue: "-1",
            // 科目
            allSubject: [],
            subjectValue: "-1",
            //年级
            allGradeList: [],
            gradeValue: "-1",
            //版本
            allVersionList: [],
            versionValue: "-1",
            treeList: [],
            defaultProps: {
                children: 'children',
                label: 'name'
            },
            attachment:[]
        }
    },
    created() {
        this.classroomSearch()
        this.teacherSearch()
        this.getAllGrade();
        this.getAllSubject();
        this.getAllClass();
        this.getAllVersion();
        this.getSubjectTree();
    },
    watch: {
        watchChange(newValue) {
            this.getSubjectTree()
        }
    },
    computed: {
        watchChange() {
            return {
                stageValue: this.stageValue,
                subjectValue: this.subjectValue,
                gradeValue: this.gradeValue,
                versionValue: this.versionValue
            }
        }
    },
    methods: {
        handleNodeClickCategory(data) {
            if (!data.hasNext) {
                this.form.catrgory = {
                    value: data.id,
                    label: data.name
                };
                // this.selectedLesson = {value:data.id,label:data.name};
                this.handleOutFocusInput();
            }
        },
        /* 获取课程树 */
        getSubjectTree() {
            const params = {
                gradeId: this.gradeValue,
                periodId: this.stageValue,
                subjectId: this.subjectValue,
                textbookVersionId: this.versionValue
            }
            this.$api.getSubjectTree(params).then(res => {
                this.treeList = res.data;
            })
        },
        /* 全部学段 字典*/
        getAllGrade() {
            const params = {
                type: "stage"
            };
            this.$api.dictGetByType(params).then(res => {
                this.allStageList = res.data;
            });
        },
        /* 全部科目 接口*/
        getAllSubject() {
            // 不传 获取 全部科目 传 学段periodId 获取对应的
            this.$api.getSubjectByGrade().then(res => {
                this.allSubject = res.data;
            });
        },
        /* 全部年级 字典*/
        getAllClass() {
            const params = {
                type: "grade"
            };
            this.$api.dictGetByType(params).then(res => {
                this.allGradeList = res.data;
            });
        },
        /* 全部版本 接口*/
        getAllVersion() {
            this.$api.getKeyAndValue().then(res => {
                this.allVersionList = res.data;
            });
        },
        handleOutFocusInput() {
            this.showSelect = false;
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(`确定移除 ${ file.name }？`);
        },
        classroomSearch() {
            this.$api.orderCourseRoomSearch().then(res => {
                this.data = this.getTree(res.data, 0)
            })
        },
        teacherSearch() {
            this.$api.orderTeacherSearch().then(res => {
                this.teacherList = res.data
                console.log(res)
            })
        },
        getTree(list, pId) {
            let re = new Array();
            for (let i = 0; i < list.length; i++) {
                let parentId = list[i].parentId;
                if (parentId == pId) {
                    let child = this.getTree(list, list[i].id);
                    if (child.length > 0) {
                        list[i].children = child;
                    }
                    re.push(list[i]);
                }
            }
            return re;
        },
        handleNodeClick(data, data1) {
            if (data.type != 0) {
                this.form.option1.name = data1.parent.data.name + "/" + data.name;
                this.form.option1.id = data.id;
                this.form.option1.pId = data1.parent.data.id;
                this.show = false;
            }
        },
        treeShow() {
            this.show = false
            this.invited.map(v => {
                v.show = false
            })
        },
        areaSelect() {
            this.show = !this.show
        },
        handleNodeClick1(data, data1) {
            let index = 0;
            this.invited.map((v, i) => {
                if (v.show) {
                    index = i;
                }
            })
            if (data.type != 0) {
                this.invited[index].name = data1.parent.data.name + "/" + data.name;
                this.invited[index].id = data.id;
                this.invited.map(v => {
                    v.show = false
                })
            }
        },
        areaSelect1(index) {
            this.invited.map((v, i) => {
                if (i == index) {
                    v.show = true
                } else {
                    v.show = false
                }
            })
        },
        addClassroom() {
            let param = {
                id: 0,
                name: '',
                show: false
            }
            this.invited.push(param)
        },
        classroomDel(index) {
            this.invited.splice(index, 1)
        },
        uploadSuccess(response, file, fileList) {
            if (response.success) {
                this.$myLayer.successLayer(response.msg)
                this.attachment = [];
                let data = {}
                fileList.map(v => {
                    data.attachmentName = v.name;
                    data.attachmentUrl = v.response.data;
                    data.attachmentSize = v.size;
                    this.attachment.push(data)
                })
            }
        },
        onSubmit(){
			if(this.courseOrder == 8001){
				let createData = {
				    courseOrder:this.courseOrder,
				    courseName:this.form.name,
				    courseTypeCode:this.form.type,
				    stageId:this.stageValue,
				    subjectId:this.subjectValue,
				    versionId:this.versionValue,
				    gradeId:this.gradeValue,
				    courseMaterialCode:this.form.catrgory.value,
				    courseTeachingRoomCode:this.form.option1.id,
				    ccourseTeachingUserId:this.form.teacher,
				    courseIntroduction:this.form.intro,
				    courseLiveCode:this.value?'1':'0',
				    attachment:this.attachment,
				    courseStartTime:this.$myLayer.timeFormat(this.form.date[0]),
				    courseEndTime:this.$myLayer.timeFormat(this.form.date[1]),
				    courseSection:this.form.num
				}
				this.$api.orderCourseDetailAdd(createData).then(res => {
					if(res.success){
						this.$myLayer.successLayer(res.msg)
					}
				})
			}else{
				let data = {};
				if(this.form.radio == 8012){
					data = {
						courseOrder:this.courseOrder,
						courseName:this.form.name,
						courseTypeCode:this.form.type,
						stageId:this.stageValue,
						subjectId:this.subjectValue,
						versionId:this.versionValue,
						gradeId:this.gradeValue,
						courseMaterialCode:this.form.catrgory.value,
						courseTeachingRoomCode:this.form.option1.id,
						ccourseTeachingUserId:this.form.teacher,
						courseIntroduction:this.form.intro,
						courseWay:this.form.radio,
						courseLiveCode:this.value?'1':'0',
						courseRecordWay:this.form.radio1,
						rooms:this.invited,
						attachment:this.attachment,
						courseStartTime:this.$myLayer.timeFormat(this.form.date[0]),
						courseEndTime:this.$myLayer.timeFormat(this.form.date[1]),
						courseSection:this.form.num
					}
				}else{
					data = {
						courseOrder:this.courseOrder,
						courseName:this.form.name,
						courseTypeCode:this.form.type,
						stageId:this.stageValue,
						subjectId:this.subjectValue,
						versionId:this.versionValue,
						gradeId:this.gradeValue,
						courseMaterialCode:this.form.catrgory.value,
						courseTeachingRoomCode:this.form.option1.id,
						ccourseTeachingUserId:this.form.teacher,
						courseIntroduction:this.form.intro,
						courseWay:this.form.radio,
						attachment:this.attachment,
						courseStartTime:this.$myLayer.timeFormat(this.form.date[0]),
						courseEndTime:this.$myLayer.timeFormat(this.form.date[1]),
						courseSection:this.form.num
					}
				}
				this.$api.orderCourseDetailAdd(data).then(res => {
					if(res.success){
						this.$myLayer.successLayer(res.msg)
					}
				})
			}
        }
    }
}
</script>

<style lang="scss" scoped="scoped">
	.container {
		padding: 0 32px;
		background-color: #F1F3F8;
		min-height: calc(100vh - 60px);

		.course-title {
			padding-top: 20px;
			margin-bottom: 24px;
			display: flex;

			div {
				&:first-child {
					color: #55595C;
					font-weight: bold;
				}
			}
		}

		.box {
			background: rgba(255, 255, 255, 1);
			box-shadow: 0px 5px 20px 0px rgba(23, 38, 71, 0.05);
			border-radius: 6px;
			padding: 22px 24px;
			width: 100%;

			.form-wrap {
				width: 564px;
			}
		}
	}

	.select-wrap {
		position: absolute;
		top: 50px;
		left: 0;
		width: 530px;
		height: 300px;
		background: #fff;
		z-index: 998;
		border-radius: 4px;
		box-shadow: 0 0 7.2px 0.8px rgba(0, 0, 0, .1);

		.select-lists {
			overflow: hidden;
			height: 240px;
		}

		.select-items {
			padding: 12px;
			display: flex;
			justify-content: space-between;

			&>div {
				width: 120px;
			}
		}
	}

	.tree-wrap {
		position: absolute;
		width: 100%;
		z-index: 99;
		border: 1px solid #dcdfe6;
		max-height: 260px;
		overflow: auto;
	}

	.icon-close {
		position: absolute;
		top: 0;
		right: -20px;
	}
</style>
<style>
	.container .el-tree {
		background: #fff;
	}
</style>
